.hamburger {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  display: none; }
  .hamburger__box {
    margin: 0 auto;
    margin-top: 30px;
    width: 30px;
    height: 30px;
    position: relative; }
  .hamburger__line {
    padding: 0;
    width: 30px;
    height: 4px;
    background-color: #262262;
    display: block;
    border-radius: 4px;
    transition: all 0.4s ease-in-out, transform 0.4s ease-in-out 0.4s;
    position: absolute; }
    .hamburger__line--1 {
      top: 0; }
    .hamburger__line--2 {
      top: 13.5px;
      width: 1px;
      transform: rotate(90deg);
      left: 13.5px; }
    .hamburger__line--3 {
      top: 13.5px;
      width: 1px;
      left: 13.5px; }
    .hamburger__line--4 {
      top: 13.5px; }
    .hamburger__line--5 {
      bottom: 0; }
  .hamburger__checkbox:checked + .hamburger__wrap .hamburger__box > .hamburger__line--1 {
    top: 13.5px;
    background-color: transparent; }
  .hamburger__checkbox:checked + .hamburger__wrap .hamburger__box > .hamburger__line--2 {
    left: 0;
    width: 30px;
    transform: rotate(45deg);
    background-color: #fbb040; }
  .hamburger__checkbox:checked + .hamburger__wrap .hamburger__box > .hamburger__line--3 {
    left: 0;
    width: 30px;
    transform: rotate(-45deg);
    background-color: #fbb040; }
  .hamburger__checkbox:checked + .hamburger__wrap .hamburger__box > .hamburger__line--4 {
    background-color: transparent; }
  .hamburger__checkbox:checked + .hamburger__wrap .hamburger__box > .hamburger__line--5 {
    bottom: 13.5px;
